	module.exports = {"data":{"page":{"id":"/media/duthaho/New Volume/learn/github/duthaho-blog/content/pages/1--about/index.md absPath of file >>> MarkdownRemark","html":"<h3>BLOG RA ĐỜI NHƯ THẾ NÀO?</h3>\n<p>Sau khoảng thời gian đọc blog của người khác thì mình cũng suy nghĩ nên một lần chơi lớn xem ai đó có trầm trồ.\nThế là đú đởn viết một cái blog để thủ dâm tinh thần chứ trình độ mình thì dell dám đứng ra mà viết blog chia sẻ kiến thức gì cho các bạn đâu nha 😚.</p>\n<h3>MÌNH LÀ THÈN CỦ CẢI NÀO?</h3>\n<ul>\n<li>Tên đầy đủ của mình là DƯơng THAnh HỢp. Nickname hay các account trên mạng đều là duthaho, nghĩa là Đ* Tha Hồ, hay Đ*t Hà Hồ gì cũng được.</li>\n<li>Tốt nghiệp Đại Học Bách Khoa Đà Nẵng khóa 08 và hiện tại đang là full-stack developer tại <a href=\"https://peddler.com\">Peddler</a>.</li>\n<li>Thời gian rãnh rỗi thì sẽ viết blog và học thêm một số thứ nữa. Hy vọng lúc đi làm có thể chém gió một chút với bên tuyển dụng.</li>\n</ul>\n<h4>Chào thân ái và quyết thắng 😝. Đừng quên comment, share và đăng ký để nhận bài viết mới hàng tuần nhé!</h4>","frontmatter":{"title":"About"}},"footnote":{"id":"/media/duthaho/New Volume/learn/github/duthaho-blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li><a href=\"https://duthaho.github.io/\" target=\"_blank\">my resume</a> - <a href=\"https://www.buymeacoffee.com/lp1cMOE\" target=\"_blank\">buy a coffee</a> - <a href=\"http://eepurl.com/gsE-Kr\" target=\"_blank\">email register</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"1476613389154628"}}}},"pathContext":{"slug":"/about/"}}

